import ButtonsComponent from "./buttonComponents";
import { useState } from "react";
import MixerCuloareRGBA from "./componente/MixerCuloriRGBA";

export default function Sistemele() {
  const [color, setColor] = useState("white");

  const handleColorChange = (event) => {
    const selectedColor = event.target.value;
    switch (selectedColor) {
      case "rou":
        setColor("red");
        break;
      case "verde":
        setColor("green");
        break;
      case "albastru":
        setColor("blue");
        break;
      case "rou+verde":
        setColor("yellow");
        break;
      case "rou+albastru":
        setColor("magenta");
        break;
      case "verde+albastru":
        setColor("cyan");
        break;
      case "Alb":
        setColor("white");
        break;
      default:
        setColor("black");
    }
  };
  return (
    <>
      <div class="flex h-auto">
        <div className="w-1/6 bg-gray-600 p-4 overflow-auto">
          <ButtonsComponent currentButton={9} />
        </div>
        <div className="w-5/6 p-4 flex flex-col bg-gray-100">
          <div className="mx-auto px-4 py-6">
          <h1>Sistemele de Culori</h1>
          <p>
            n lumea designului digital, nelegerea i utilizarea corect a
            sistemelor de culori este esenial. Fie c este vorba de crearea
            unui website, designul unei aplicaii sau elaborarea materialelor
            grafice, cunoaterea modului n care culorile sunt reprezentate i
            manipulate poate avea un impact semnificativ asupra rezultatului
            final.{" "}
          </p>
          <h4>
            Sistemele RGB, RGBA, HEX, CMYK i HSL sunt fundamentale pentru orice
            designer digital.
          </h4>
          <h2>1.RGB (Red, Green, Blue)</h2>

          <p>
            2.RGB este un model de culoare aditiv folosit predominant n
            tehnologiile digitale care emit lumin, precum monitoare,
            televizoare i ecrane de telefon. Acest model se bazeaz pe teoria
            comform creia lumina alb poate fi obinut prin suprapunerea
            luminilor roii, verzi i albastre. Culorile sunt create prin
            iluminarea subpixelilor.
          </p>

          <p>
            n modelul RGB, fiecare culoare este reprezentat ca o combinaie a
            trei valori, corespunznd intensitilor rou, verde i albastru.
            Fiecare component poate avea o valoare ntre 0 (absena complet a
            culorii) i 255 (prezena complet a culorii), ceea ce ofer
            posibilitatea de a reprezenta pn la 16,777,216 de culori diferite.
          </p>
          <h4 className="w-full md:w-2/6 mt-4">Mixer de culori RGB</h4>
          <div className="flex w-full items-center h-auto">
  <div className="w-3/5">
    <p>Toate culorile vor lua valoarea 255 (cea mai mare)</p>
  </div>
  <div className="flex mb-10 flex-col justify-center items-center text-center w-2/5">
    <select
      onChange={handleColorChange}
      className="form-select appearance-none w-auto px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding bg-no-repeat border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
    >
      <option value="Alb">Alb</option>
      <option value="rou">Rou</option>
      <option value="verde">Verde</option>
      <option value="albastru">Albastru</option>
      <option value="rou+verde">Rou + Verde</option>
      <option value="rou+albastru">Rou + Albastru</option>
      <option value="verde+albastru">Verde + Albastru</option>
      <option value="rou+verde+albastru">Rou + Verde + Albastru</option>
    </select>

    <div
      className="m-auto w-24 h-24 flex items-center justify-center text-white text-center"
      style={{ backgroundColor: color }}
    >
      Culoarea Rezultat
    </div>
  </div>
</div>


          <h2>RGBA (Red, Green, Blue, Alpha)</h2>
          <p>
            RGBA adaug o component de transparen denumit Alpha la modelul
            RGB. Aceast component controleaz opacitatea culorii, unde 0
            reprezint complet transparent i 1 reprezint complet opac.
          </p>
          <p>
            Alpha este folosit pentru a crea efecte de suprapunere sau
            transparen n imagini i design-uri, permind vizibilitatea
            elementelor de sub stratul superior.
          </p>
          <p>
            Este extrem de util n designul web i grafic digital, RGBA
            permite designerilor s suprapun elemente fr a pierde informaii
            vizuale din spatele elementelor transparente.
          </p>

          <MixerCuloareRGBA />
          <h2>3. HEX (Hexadecimal Color Model)</h2>
          <p>
            HEX reprezint o metod de codificare a informaiilor despre culoare
            n format hexazecimal. Este o form compact a modelului RGB, unde
            fiecare component de culoare este reprezentat de dou cifre
            hexazecimale.
          </p>
          <p>
            Fiecare pereche poate varia ntre 00 i FF n sistemul hexazecimal,
            unde 00 reprezint absena complet a culorii respective i FF
            reprezint prezena maxim a culorii.
          </p>
          <p>
            Un cod HEX este format dintr-un simbol # urmat de ase cifre.
            Primele dou cifre reprezint intensitatea rou, urmtoarele dou
            verde, iar ultimele dou albastru. De exemplu, #FFFFFF reprezint
            alb, iar #000000 este negru.
          </p>
          <p>
            HEX este predominant folosit n designul web i n specificaiile de
            culoare n stilurile CSS, datorit uurinei de utilizare i a
            compatibilitii universale n browsere.
          </p>

          <h2>4. CMYK (Cyan, Magenta, Yellow, Key/Black)</h2>
          <p>
            CMYK este un model de culoare substractiv, utilizat n principal
            pentru imprimare. Acesta funcioneaz prin absorbia anumitor
            lungimi de und ale luminii, opusul modelului aditiv RGB.
          </p>
          <p>
            Culorile n CMYK sunt create prin combinarea diferitelor niveluri de
            cyan, magenta, galben i negru. Creterea intensitii fiecrei
            culori reduce cantitatea de lumin reflectat, genernd o gam larg
            de nuane.
          </p>
          <p>
            CMYK este esenial n industria tipografic pentru a asigura
            imprimarea corespunztoare ateptrilor din design-ul digital, fiind
            utilizat pentru tot de la cri i reviste, la afie i brouri.
          </p>

          <h2>5. HSL (Hue, Saturation, Lightness)</h2>
          <p>
            HSL este un model de culoare alternativ la RGB, conceput pentru a fi
            mai intuitiv. Nuana (Hue) este reprezentat ca un grad pe cercul de
            culori, saturaia (Saturation) ca un procentaj, iar luminozitatea
            (Lightness) de asemenea ca un procentaj.
          </p>
          <p>
            Nuana (Hue) este un unghi de la 0 la 360 grade, saturaia este
            procentul de culoare pur n combinaie, iar luminozitatea indic
            ct de apropiat este de alb (100%) sau negru (0%).
          </p>
          <p>
            Modelul HSL este frecvent utilizat n software-urile de editare foto
            i n designul web, oferind designerilor o metod mai direct de a
            ajusta culorile fr a modifica fiecare component RGB individual.
          </p>

          <h2>Concluzie</h2>
          <p>
            nelegerea i aplicarea corespunztoare a acestor modele de culoare
            sunt cruciale pentru succesul n domeniul designului digital.
            Fiecare model are particularitile sale i \ este adaptat pentru
            utilizri specifice, oferind creatorilor de coninut digital
            instrumentele necesare pentru a realiza vizualuri atractive i
            eficiente.
          </p>
        </div>
        </div>
      </div>
    </>
  );
}
